<template>
  <div :style='{height: ht + "px"}' style='position: relative; width: 100%;'>
    <img src="../assets/reg_bg@2x.png" style='width: 100%; height: 99.5%;' @click="goBack"/>
    <div style='position: absolute; top: 0px; left: 0px; width: 100%; text-align: center;'>
      <van-icon @click='$router.push("/mine")' name="arrow-left" color='#fff' size='40'
                style='margin-top: 20px; margin-left: 10px; float: left;'/>
      <div style='width: 90%; margin-top: 9%; height: auto; display: inline-block; box-sizing: border-box; padding: 40px 20px; text-align: left;'>
        <div style='color:rgba(250, 84, 28, 1); font-weight: bold; font-size: 30px;'>注册</div>
        <div class='inputBg'>
          <img src="../assets/shouji@2x.png"/>
          <input style="font-size: 16px" v-model='phone' placeholder='请输入您的手机号码'/>
        </div>
        <div class='inputBg'>
          <img src="../assets/yzm@2x.png"/>
          <input v-model='yzm' placeholder='请输入验证码' style='width: 100px;font-size: 16px;'/>
          <!--<div @click='queryYZM'-->
               <!--&gt;-->
            <!--获取验证码-->
          <!--</div>-->
          <van-button class="code_btn" :disabled="unClick" @click='queryYZM'>{{unClick?`${time}s`:'获取验证码'}}</van-button>
        </div>
        <div class='inputBg'>
          <img src="../assets/mima@2x.png"/>
          <input style="font-size: 16px" type="password" v-model='pass' placeholder='请输入您的登陆密码'/>
        </div>
        <div class='inputBg'>
          <img src="../assets/mima@2x.png"/>
          <input style="font-size: 16px" type="password" v-model='passAgain' placeholder='请确认您的登陆密码'/>
        </div>


        <div style='color: rgba(250, 84, 28, 1); width: 100%; height: 30px; margin-top: 7%; margin-bottom: 8px;'>
          <span style='float: left;'>注册即同意《注册协议》</span>
          <span @click='$router.push("/login")' style='float: right;'>去登录</span>
        </div>
        <div style='width: 100%; height: 52px; font-size: 22px; text-align: center;line-height: 52px; border-radius: 30px; color: #fff; background:linear-gradient(90deg,rgba(255,142,106,1) 0%,rgba(250,84,28,1) 100%);'
             @click="goToRegister">马上注册
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "zhuce",
    data() {
      return {
        ht: window.screen.height,
        phone: '',
        yzm: '',
        pass: '',
        passAgain: '',

        unClick: false,
        time: 60,
      }
    },
    mounted() {

    },
    methods: {
      goBack(){
        this.$router.back(-1);
      },
      async queryYZM() {
        let self = this
        if (self.phone.length !== 11) {
          self.$toast('请输入正确的手机号码')
          return
        }
        this.unClick = true;
        this.changeTime();
        const data = await self.$service.login.sendSms({
          username: self.phone,
          type: 'register'
        })
        if(data.status === 200){
            self.$toast('注册成功，请前往登陆')
        }
      },
      changeTime(){
        setInterval(()=>{
          if(this.time >0 && this.unClick){
            this.time = this.time-1;
          } else {
            this.time = 60;
            this.unClick = false;
          }
        },1000)
      },
      async goToRegister() {
        if (!this.phone) {
          this.$toast('请输入手机号码');
          return;
        }
        if (!this.yzm) {
          this.$toast('请输入验证码');
          return;
        }
        if (!this.pass) {
          this.$toast('请输入密码');
          return;
        }
        if (!this.passAgain) {
          this.$toast('请确认密码');
          return;
        }
        if (this.passAgain !== this.pass) {
          this.$toast('密码不一致，请确认');
          return;
        }
        const {err,data} = await this.$service.login.doRegister({
          username: this.phone,
          code: this.yzm,
          password: this.pass,
          password_confirm: this.passAgain
        })
        if(err){
          this.$toast(err.message);
          return;
        }
        console.log(data)
      },
    }
  }
</script>

<style scoped>
  .inputBg {
    width: 100%;
    height: 52px;
    margin-top: 15px;
    line-height: 52px;
    border-radius: 30px;
    background: rgba(247, 247, 247, 1);
  }

  .inputBg img {
    width: 30px;
    float: left;
    margin: 11px 20px;
  }

  .inputBg input {
    float: left;
    height: 50px;
    background: none;
    color: rgba(191, 191, 191, 1);
    border: none;
    font-size: 18px;
  }
  .code_btn{
    color: #fff;
    margin-top: 8px;
    background: rgba(250, 84, 28, 1);
    width: 90px;
    height: 36px;
    line-height: 18px;
    border-radius: 18px;
    padding: 4px 6px;
    border: none;
    float: right;
  }
</style>